"use strict";

import gsap from "gsap";
import ScrollTrigger from 'gsap/ScrollTrigger';
import scroll from "../helpers/scroll";
import { addClass, removeClass, hoverEvents } from "../helpers/helper";

var meview = {
	namespace: 'me',
	beforeLeave: function (data) {
		var current = data.current.container;
		removeClass(current.querySelector(".main-text h1 strong"), "emphasis");
	},
	beforeEnter: function (data) {
		var next = data.next.container;

		// Main text
		next.querySelectorAll("#about").forEach(function (element) {
			var text = element.querySelectorAll(".main-text");

			ScrollTrigger.matchMedia({
				"(min-aspect-ratio: 1/1)": () => {
					gsap.set(text, {
						position: "relative",
						pointerEvents: "auto",
						opacity: 1
					});
				},
				"(min-aspect-ratio: 1/1)": () => {
					scroll.push(tl => {
						// Stick on pos y
						tl.set(text, {
							position: "fixed",
							top: "50%",
							y: "-50%"
						}, 0);

						tl.fromTo(text, {
							pointerEvents: "auto"
						}, {
							pointerEvents: "none",
							duration: 1
						}, 0);

						tl.fromTo(text, {
							opacity: 1
						}, {
							opacity: 0,
							ease: 'expo.out',
							duration: .5
						}, 0);

						tl.set(text, {
							position: "relative"
						}, 1);

						return tl;
					}, tl => {
						return ScrollTrigger.create({
							trigger: element,
							start: "0 0",
							end: "100% 0",
							animation: tl,
							scrub: true
						});
					});
				}
			});
		});

		// Big red dot
		next.querySelectorAll("#usedto").forEach(function (element) {
			var middle = element.querySelectorAll(".middle");

			ScrollTrigger.matchMedia({
				"(max-aspect-ratio: 1/1)": () => {
					gsap.set(middle, {
						display: "flex",
						pointerEvents: "auto",
						x: 0,
						y: 0,
						opacity: 1,
						scale: 1
					});
					middle.forEach(function (el) {
						// Move in
						gsap.set(el.querySelectorAll(".text > *"), {
							y: 0,
							opacity: 1
						});
					});
				},
				"(min-aspect-ratio: 1/1)": () => {
					// Stick on pos y when showing and hiding
					scroll.push(tl => {
						tl.set(middle, {
							position: "fixed",
							top: "50%",
							y: "-50%",
							pointerEvents: "none"
						}, 0);

						tl.set(middle, {
							pointerEvents: "auto"
						}, 1);

						tl.set(middle, {
							position: "absolute",
							pointerEvents: "none"
						}, 4);

						return tl;
					}, tl => {
						return ScrollTrigger.create({
							trigger: element,
							start: "0 100%",
							end: "400% 100%",
							animation: tl,
							scrub: true
						});
					});

					// Fade in and scale down
					scroll.push(tl => {
						// Red dot - fade in
						tl.fromTo(middle, {
							display: "none",
							opacity: 0,
						}, {
							display: "flex",
							opacity: 1,
							ease: 'expo.in',
							duration: .25
						}, 0);

						// Red dot - scale down in
						tl.fromTo(middle, {
							x: 0,
							scale: 3,
						}, {
							x: 0,
							scale: 1,
							duration: .75
						}, 0);

						// Text - move in, fade in, and fade out
						middle.forEach(function (el) {
							// Move in
							tl.fromTo(el.querySelectorAll(".text > *"), {
								y: 200,
							}, {
								y: 0,
								ease: 'expo.out',
								duration: 1
							}, 0);

							// Fade in
							tl.fromTo(el.querySelectorAll(".text > *"), {
								opacity: 0,
							}, {
								opacity: 1,
								ease: 'expo.out',
								duration: .5
							}, .125);
						});

						return tl;
					}, tl => {
						return ScrollTrigger.create({
							trigger: element,
							start: "0 100%",
							end: "75% 100%",
							animation: tl,
							scrub: 1
						});
					});

					// Move to left and scale down
					scroll.push(tl => {
						// Red dot - Move left
						tl.fromTo(middle, {
							x: 0,
							scale: 1,
						}, {
							x: window.innerWidth * -1 / 2,
							scale: .15,
							duration: 1
						}, 0);

						// Text - fade out
						middle.forEach(function (el) {
							// Fade out
							tl.fromTo(el.querySelectorAll(".text > *"), {
								opacity: 1,
							}, {
								opacity: 0,
								ease: 'expo.out',
								duration: .5
							}, .5);
						});

						return tl;
					}, tl => {
						return ScrollTrigger.create({
							trigger: element,
							start: "100% 100%",
							end: "175% 100%",
							animation: tl,
							scrub: 1
						});
					});

					// Fade out dot
					scroll.push(tl => {
						// Red dot - fade out
						tl.fromTo(middle, {
							opacity: 1,
						}, {
							opacity: 0,
							ease: 'expo.out',
						});

						return tl;
					}, tl => {
						return ScrollTrigger.create({
							trigger: element,
							start: "300% 100%",
							end: "375% 100%",
							animation: tl,
							scrub: true
						});
					});
				}
			});
		});

		// Photo of me on canoe
		next.querySelectorAll("#now").forEach(function (element) {
			scroll.push(tl => {
				tl.fromTo(element.querySelectorAll('.thumbs'), {
					opacity: 0,
					x: -40,
					y: (window.innerHeight * -2 / 3),
				}, {
					opacity: 1,
					x: -10,
					y: 0,
					ease: 'linear'
				});

				return tl;
			}, tl => {
				return ScrollTrigger.create({
					trigger: element,
					start: "0 50%",
					end: "50% 50%",
					animation: tl,
					scrub: true
				});
			});

			scroll.push(tl => {
				tl.fromTo(element.querySelectorAll('.thumbs'), {
					x: -10,
					y: 0,
				}, {
					x: 0,
					y: (window.innerHeight * 1) + 25,
					ease: 'linear'
				});

				return tl;
			}, tl => {
				return ScrollTrigger.create({
					trigger: element,
					start: "50% 50%",
					end: "150% 50%",
					animation: tl,
					scrub: true
				});
			});

			scroll.push(tl => {
				tl.fromTo(element.querySelectorAll('.thumbs'), {
					opacity: 1,
					x: 0,
					y: (window.innerHeight * 1) + 25
				}, {
					opacity: 0,
					x: 20,
					y: (window.innerHeight * 1.75) + 25,
					ease: 'linear'
				});

				return tl;
			}, tl => {
				return ScrollTrigger.create({
					trigger: element,
					start: "150% 50%",
					end: "200% 50%",
					animation: tl,
					scrub: true
				});
			});
		});

		// Now, Webdesigner, and Sayhi sections
		next.querySelectorAll("#now, #webdesigner, #sayhi").forEach(function (element) {
			var isSayHi = element.getAttribute('id') == "sayhi";
			var el = isSayHi ? element.querySelectorAll('.text > div > *, .text > p') : element.querySelectorAll('.text > *');
			var animation = function (horizontal = true) {
				isSayHi = horizontal ? isSayHi : false;

				// Fade in and fade out
				scroll.push(tl => {
					el.forEach(function (el, idx) {
						tl.fromTo(el, {
							opacity: 1
						}, {
							opacity: 0,
							ease: 'linear',
							duration: .25
						}, idx / 5);
					});

					return tl;
				}, tl => {
					return ScrollTrigger.create({
						trigger: element,
						start: "50% 50%",
						end: "50% -50%",
						animation: tl,
						scrub: .5
					});
				});

				// Slide in
				scroll.push(tl => {
					el.forEach(function (el, idx) {
						tl.fromTo(el, {
							x: isSayHi && idx < 2 ? idx * -50 : 0,
							y: isSayHi && idx < 2 ? 0 : idx * 50
						}, {
							x: 0,
							y: 0,
							ease: 'linear'
						}, 0);
					});

					return tl;
				}, tl => {
					return ScrollTrigger.create({
						trigger: element,
						start: "50% 150%",
						end: "50% 50%",
						animation: tl,
						scrub: .5
					});
				});

				// Slide out
				scroll.push(tl => {
					el.forEach(function (el, idx) {
						tl.fromTo(el, {
							x: 0,
							y: 0
						}, {
							x: isSayHi && idx < 2 ? idx * -25 : 0,
							y: isSayHi && idx < 2 ? 0 : idx * -25,
							ease: 'linear'
						}, 0);
					});

					return tl;
				}, tl => {
					return ScrollTrigger.create({
						trigger: element,
						start: "50% 50%",
						end: "50% -50%",
						animation: tl,
						scrub: .5
					});
				});
			}

			ScrollTrigger.matchMedia({
				"(max-aspect-ratio: 1/1)": () => {
					animation(false);
				},
				"(min-aspect-ratio: 1/1)": () => {
					animation();
				}
			});
		});

		// Webdesigner section
		gsap.set(next.querySelectorAll("#webdesigner .thumbs"), {
			display: "none"
		});

		// Spinning Mr. Goat and Pinning
		next.querySelectorAll("#mrgoat").forEach(element => {
			// Statics
			var duration = 1;
			var imgs = element.querySelectorAll(".thumbs > img");
			var mrgoat = {
				frame: 1
			};
			var prev = false;
			// Defining
			var repeat = 5;
			// hide
			scroll.push(tl => {
				tl.fromTo(element, {
					y: "0%"
				}, {
					y: "-100%",
					ease: "linear"
				});

				return tl;
			}, tl => ScrollTrigger.create({
				trigger: "#endmrgoat",
				start: "0 0",
				end: "100% 0",
				animation: tl,
				scrub: true
			}));
			// Show
			scroll.push(tl => {
				tl.set(element, {
					position: "fixed",
					top: 0
				});

				tl.fromTo(element, {
					y: "100%"
				}, {
					y: "0%",
					ease: "linear"
				});

				return tl;
			}, tl => ScrollTrigger.create({
				trigger: "#startmrgoat",
				start: "0 100%",
				end: "100% 100%",
				animation: tl,
				scrub: true
			}));
			// Spinning
			gsap.set(imgs, { opacity: 0 });
			scroll.push(tl => {
				tl.to(mrgoat, {
					frame: imgs.length,
					snap: "frame",
					repeat: repeat + 2,
					ease: "linear",
					duration: (repeat + 2) * duration,
					onUpdate: () => {
						var frame = mrgoat.frame + 4;
						var el;

						if (frame > imgs.length) frame -= imgs.length;
						el = element.querySelector(".mrgoat" + frame);

						if (prev) prev.style.opacity = 0;
						el.style.opacity = 1;

						prev = el;
					}
				}, 0);

				return tl;
			}, tl => ScrollTrigger.create({
				trigger: "#startmrgoat",
				endTrigger: "#endmrgoat",
				start: "0 100%",
				end: "100% 0",
				animation: tl,
				scrub: .5
			}));
			// Facts
			scroll.push(tl => {
				var el = element.querySelectorAll(".thumbs, .text");
				var facts = function (els) {
					var dur = duration / 7.5;
					var tl = gsap.timeline();

					tl.fromTo(els, {
						opacity: 0
					}, {
						opacity: 1,
						ease: "power3.in",
						duration: dur
					});
					els.forEach(function (el) {
						tl.fromTo(el.querySelectorAll(".dot hr"), {
							width: "0%"
						}, {
							width: "100%",
							duration: dur
						});
					});
					els.forEach(function (el) {
						tl.fromTo(el.querySelectorAll(".line hr"), {
							width: "0%"
						}, {
							width: "100%",
							duration: dur
						});
					});
					els.forEach(function (el) {
						tl.fromTo(el.querySelectorAll("p"), {
							opacity: 0
						}, {
							opacity: 1,
							duration: dur
						});
					});
					tl.fromTo(els, {
						y: 100
					}, {
						y: -100,
						ease: "linear",
						duration: (dur * 10)
					}, 0);
					tl.to(els, {
						opacity: 0,
						ease: "power3.out",
						duration: dur
					}, (dur * 9));

					return tl;
				}

				var array = [0, 1, 2, 3];
				gsap.utils.shuffle(array);
				tl.add(facts(element.querySelectorAll("#viet")), (duration * array[0]));
				tl.add(facts(element.querySelectorAll("#food")), (duration * array[2]));
				tl.add(facts(element.querySelectorAll("#nyc")), (duration * array[1]));
				tl.add(facts(element.querySelectorAll("#travel")), (duration * array[3]));

				tl.to(el, {
					duration: repeat * duration
				}, 0);

				return tl;
			}, tl => {
				return ScrollTrigger.create({
					// scroller: next,
					trigger: "#startmrgoat",
					endTrigger: "#endmrgoat",
					start: "0 0",
					end: "100% 100%",
					animation: tl,
					scrub: .5
				});
			});
			// Pinning
			scroll.push(tl => {
				tl.fromTo(element.querySelectorAll("#post > *"), {
					opacity: 0
				}, {
					opacity: 1,
					ease: "power3.out",
					duration: duration
				}, (duration * 4));
				tl.fromTo(element.querySelectorAll("#post > *"), {
					y: window.innerHeight
				}, {
					y: 0,
					ease: "linear",
					duration: duration
				}, (duration * 4));
				tl.fromTo(element.querySelectorAll(".text .h2"), {
					opacity: 1
				}, {
					opacity: 0,
					ease: "power3.in",
					duration: duration
				}, (duration * 4));

				return tl;
			}, tl => {
				return ScrollTrigger.create({
					// scroller: next,
					trigger: "#startmrgoat",
					endTrigger: "#endmrgoat",
					start: "0 0",
					end: "100% 100%",
					animation: tl,
					scrub: true
				});
			});
		});

		// Animate cofounder
		next.querySelectorAll(".cofound").forEach(el => {
			// Hover
			var picture = el.querySelector("picture");
			hoverEvents(el.querySelectorAll("a"), () => {
				gsap.killTweensOf(picture);
				gsap.to(picture, {
					scale: 2,
					duration: 120,
					ease: "linear"
				});
			}, () => {
				gsap.killTweensOf(picture);
				gsap.to(picture, {
					scale: 1,
					duration: 1,
					ease: "expo.out"
				});
			});
			// Animate
			var eltext = el.querySelectorAll(".text");
			scroll.push(tl => {
				tl.fromTo(eltext, {
					y: window.innerHeight * 1 / 10
				}, {
					y: window.innerHeight * -1 / 10,
					ease: "linear",
					duration: 4
				}, 0);
				tl.fromTo(eltext, {
					opacity: 0
				}, {
					opacity: 1,
					ease: "linear",
					duration: 1
				}, 0);
				tl.fromTo(eltext, {
					opacity: 1
				}, {
					opacity: 0,
					ease: "linear",
					duration: 1
				}, 3);

				tl.fromTo(el.querySelectorAll(".thumbs"), {
					y: window.innerHeight * -1 / 4
				}, {
					y: window.innerHeight * 1 / 4,
					ease: "linear",
					duration: 4
				}, 0);
				return tl;
			}, tl => ScrollTrigger.create({
				trigger: el,
				start: "0 100%",
				end: "100% 0",
				animation: tl,
				scrub: true
			}));
		});

		// Links
		next.querySelectorAll(".links").forEach(el => {
			scroll.moveText({
				elements: el.querySelectorAll("nav > *"),
				position: "100%"
			});
		});

		// Snap
		next.querySelectorAll("section.middle, .links").forEach(el => {
			scroll.snap(el);
		});

		// Refresh
		ScrollTrigger.refresh();
	},
	afterEnter: data => {
		var next = data.next.container;

		addClass(next.querySelector(".main-text h1 strong"), "emphasis");

		console.info("Hello, my name is Dwan! I'm a UI/UX Designer and Strategist a.k.a. Web Designer in Steroid")
	}
}

export default meview;